.Process {
  height: 100%;
  margin-top: 2rem;

  align-items: center;
  text-align: center;
}

.images {
  justify-content: space-between;
  height: 350px;
}
.images:hover {
  transform: scale(1.2);
  transition: 2s;
}
.monogram {
  margin-right: 40px;

  height: inherit;
}
.image_wrapper {
  justify-content: space-between;
  margin: 0rem 2rem;
  display: flex;
  align-items: center;
  text-align: center;
  // height: fit-content;
}
.marquee {
  height: 100px;
  overflow: hidden;
  position: relative;
  background: #fefefe;
  color: #333;
  border-top: 5px solid #8f2cd1;
  border-bottom: 5px solid #8f2cd1;
  margin: 2.5rem 0rem;
}
